<template>
  <div class="page">
    <AnimateHeader
    bg-color="#f5f5f5"
    menu-bg-color="#f0f0f0"
    color="#3A3A3A"
    title="绑定微信"
    :on-back-button-click="back"
    :expanded="false"
    :animate="false"
    >
    </AnimateHeader>
    <div class="page-content unbind-weixin">
      <div class="time-note">一个微信号单月内最多可以绑定两次</div>
      <div v-if="user_profiles.wx_nickname" class="tip">已绑定微信</div>
      <div :class="{'no-nickname' : !user_profiles.wx_nickname}" class="wx-nickname">{{user_profiles.wx_nickname ? user_profiles.wx_nickname : '已绑定微信'}}</div>
      <div class="icon"><img src="//qianka.b0.upaiyun.com/images/9925f0a79cbaa6a41643d2e932f45bce.png" alt=""></div>
      <button class="button button-unbind" @click="unbindWechat()">解绑微信</button>
    </div>
  </div>
</template>
<style lang='scss' scoped>
  @import "../../../scss/mixins";

  .unbind-weixin {
    padding-top: 50px;
    background: #FFFFFF;
    text-align: center;

    .time-note {
      background: #FFF8EE;
      text-align: center;
      font-size: 12px;
      color: #3A3A3A;
      line-height: 40px;
      margin: 20px auto;
      width: 280px;
      height: 40px;

      @include font-regular();
    }

    .tip {
      font-size: 12px;
      color: #b5b5b5;
      margin-top: 60px;
      margin-bottom: 20px;
      height: 12px;
      line-height: 12px;

      @include font-regular();
    }

    .wx-nickname {
      font-size: 24px;
      height: 24px;
      line-height: 24px;
      margin-bottom: 35px;

      &.no-nickname {
        margin-top: 60px;
        font-size: 16px;
        font-weight: 600;
        text-align: center;
        color: #3a3a3a;
      }

      @include font-regular();
    }

    .icon {
      height: 10px;
      margin-bottom: 45px;

      img {
        width: 27px;
        height: 10px;
      }
    }

    .button-unbind {
      width: 172px;
      min-height: 40px;
      height: 40px;
      line-height: 40px;
      font-size: 13px;
      background: #ef502b;
      color: #FFFFFF;
      border-radius: 2px;

      @include font-semibold();
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import {mapState, mapActions} from 'vuex'

  export default {
    components: {
      AnimateHeader
    },

    computed: {
      ...mapState('user', [
        'user_profiles'
      ])
    },

    mounted () {
      this.getProfile()
    },

    methods: {
      back () {
        $router.back({
          path: '/user/profile'
        })
      },

      ...mapActions('user', [
        'getProfile',
        'unbindWechat'
      ])
    }
  }
</script>



